function mainSlider() {
    var swiper = new Swiper('.main-slider .swiper-container', {
        slidesPerView: 1,
        spaceBetween: 0,
        allowTouchMove: false,
        effect: 'fade',
        fadeEffect: {
            crossFade: true
        },
        breakpoints: {
            320: {
                allowTouchMove: true,
            },
            992: {
                allowTouchMove: false
            }
        }
    })

    $(".main-preview__item").mouseover(function () {
        let tab = +$(this).attr("data-tab")
        $(".main-preview__item").removeClass("_active")
        $(this).addClass("_active")
        swiper.slideTo(tab)
    })
}

function clientsSlider() {
    var swiper = new Swiper('.clients .swiper-container', {
        slidesPerView: "auto",
        spaceBetween: 0,
        scrollbar: {
            el: '.clients .swiper-scrollbar',
            draggable: true,
        },
    })
}

function workSlider() {
    var swiper = new Swiper('.work-slider .swiper-container', {
        slidesPerView: "auto",
        spaceBetween: 0,
        direction: "vertical",
        breakpoints: {
            320: {
                direction: "horizontal",
            },
            992: {
                direction: "vertical",
            }
        }
    })

    swiper.on('slideChange', function () {
        $(".works__tab").removeClass("_active")
        $(`.works__tab[data-tab="${swiper.activeIndex}"]`).addClass("_active")
    });

    $(".works__tab").click(function () {
        let tab = +$(this).attr("data-tab")
        $(".works__tab").removeClass("_active")
        $(this).addClass("_active")
        swiper.slideTo(tab)
    })
}

function historySlider() {
    var swiper = new Swiper('.history-slider .swiper-container', {
        slidesPerView: "auto",
        spaceBetween: 0,
        scrollbar: {
            el: '.history-slider .swiper-scrollbar',
            draggable: true,
        },
    })

    swiper.on('slideChange', function () {
        $(".history__tab").removeClass("_active")
        $(`.history__tab[data-tab="${swiper.activeIndex}"]`).addClass("_active")
    });

    $(".history__tab").click(function () {
        let tab = +$(this).attr("data-tab")
        $(".history__tab").removeClass("_active")
        $(this).addClass("_active")
        swiper.slideTo(tab)
    })
}


mainSlider()
clientsSlider()
workSlider()
historySlider()
